body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100%;
}

.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;;

}

.App-logo {
  -webkit-animation: App-logo-spin infinite 10s linear;
          animation: App-logo-spin infinite 10s linear;
  height: 40vmin;
}

.App-header {
  /* background-color: #282c34; */
  background-image: url(/react_memory/static/media/plexus_abstract_1080.39098b3b.png);
  background-size: cover;

  padding-top: 125px;
  padding-bottom: 50px;
  /* min-height: 100vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* font-size: calc(10px + 2vmin); */
  font-size: 1.5em;
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.header-instructions {
  width: 50%;
}


.win-game {
  -webkit-animation: win-game 1s ease;
          animation: win-game 1s ease;
}


@-webkit-keyframes win-game {
  0%    { border: none; }
  50%   { border: 5px solid green; }
  100%  { border: none; }
}


@keyframes win-game {
  0%    { border: none; }
  50%   { border: 5px solid green; }
  100%  { border: none; }
}

.shake {
  -webkit-animation: shake .5s 0.125s;
          animation: shake .5s 0.125s;
}

@-webkit-keyframes shake {
  0% { -webkit-transform: translate(5px, 0px); transform: translate(5px, 0px); }
  10% { -webkit-transform: translate(-10px, 0px); transform: translate(-10px, 0px); }
  25% { -webkit-transform: translate(10px, 0px); transform: translate(10px, 0px); }
  40% { -webkit-transform: translate(-10px, 0px); transform: translate(-10px, 0px); }
  55% { -webkit-transform: translate(10px, 0px); transform: translate(10px, 0px); }
  70% { -webkit-transform: translate(-10px, 0px); transform: translate(-10px, 0px); }
  85% { -webkit-transform: translate(10px, 0px); transform: translate(10px, 0px); }
  100% { -webkit-transform: translate(-5px, 0px); transform: translate(-5px, 0px); }
}

@keyframes shake {
  0% { -webkit-transform: translate(5px, 0px); transform: translate(5px, 0px); }
  10% { -webkit-transform: translate(-10px, 0px); transform: translate(-10px, 0px); }
  25% { -webkit-transform: translate(10px, 0px); transform: translate(10px, 0px); }
  40% { -webkit-transform: translate(-10px, 0px); transform: translate(-10px, 0px); }
  55% { -webkit-transform: translate(10px, 0px); transform: translate(10px, 0px); }
  70% { -webkit-transform: translate(-10px, 0px); transform: translate(-10px, 0px); }
  85% { -webkit-transform: translate(10px, 0px); transform: translate(10px, 0px); }
  100% { -webkit-transform: translate(-5px, 0px); transform: translate(-5px, 0px); }
}
.gamepiece {
    width: 170px;
    height: 170px;
    border: 5px solid #f1f1f1;
    margin: 8px 25px;
    border-radius: 4px;
    cursor: pointer;
}

.gamepiece:hover {
    box-shadow: 0 14px 28px rgba(0,0,0,.25),0 10px 10px rgba(0,0,0,.22);
    box-shadow: 0 7px 20px rgba(255, 255, 255, .9),0 10px 10px rgba(255, 255, 255, .22);
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    -webkit-transition: 0.1s;
    transition: 0.1s;
}

.gamepiece img {
    width: 100%;
    height: 100%;
}



.gameboard {
    display: flex;
    min-height: 80vh;
    flex-wrap: wrap;
    justify-content: center;
    align-content: space-around;
    /* width: 90%; */
    max-width: 1100px;
    margin: 0 auto;
    margin-top: 50px;

    padding-left: 100px;
    padding-right: 100px;

}


.navbar {
    position: fixed;
    z-index: 5;
    width: 100%;
    padding: 0;
    background-color: #5133a4;
    font-size: calc(10px + 14 * (100vw - 320px)/ 800);

    color: white;

    box-shadow: 0 10px 20px black;
}

ul {
    width: 100%;
    padding: 0;
    margin: 0;
}

.navbar li {
    display: inline-block;
    width: 33.3%;
    text-align: center;
    padding: 25px 0px;
    /* overflow-x: visible; */
}

.brand {
    font-weight: bold;
}
